/*
 * popupCls弹出样式
 * 应用于free-board-dom
 */

.popupCls {
  transition: .1s all linear;
  -webkit-transition: .1s all linear;
  width: 200px;
  height: 300px;
  transform: scale(.5);
  -webkit-transform: scale(0);
  position: fixed;
  background-color: rgba(0, 0, 0, .6);
  border-radius: 4px;
  z-index: 9999;
  pointer-events: none;
  opacity: 0;
}

.popupCls.hoverCls {
  transform: scale(1);
  -webkit-transform: scale(1);
  opacity: 1;
}

.popupCls .pop_img {
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  background-color: #ddd;
  width: 180px;
  height: 130px;
  margin: 10px;
}

.popupCls .pop_title {
  color: #fff;
  font-size: 14px;
  font-weight: bold;
  height: 130px;
  margin: 10px;
}


