/* green */

#alert-form .icheckbox_square-green,
#alert-form .iradio_square-green {
  display: inline-block;
  // *display: inline;
  vertical-align: middle;
  margin: 0;
  padding: 0;
  width: 18px;
  height: 18px;
  background: url(green.png) no-repeat;
  border: none;
  cursor: pointer;
}

#alert-form .icheckbox_square-green {
  background-position: 0 0;
}

#alert-form .icheckbox_square-green.hover {
  background-position: -24px 0;
}

#alert-form .icheckbox_square-green.checked {
  background-position: -50px -2px;
}

.icheckbox_square-green.disabled {
  background-position: -72px 0;
  cursor: default;
}

#alert-form .icheckbox_square-green.checked.disabled {
  background-position: -96px 0;
}

#alert-form .iradio_square-green {
  background-position: -120px 0;
}

#alert-form .iradio_square-green.hover {
  background-position: -144px 0;
}

#alert-form .iradio_square-green.checked {
  background-position: -168px 0;
}

#alert-form .iradio_square-green.disabled {
  background-position: -192px 0;
  cursor: default;
}

#alert-form .iradio_square-green.checked.disabled {
  background-position: -216px 0;
}


/* HiDPI support */

@media (-o-min-device-pixel-ratio: 5/4),
(-webkit-min-device-pixel-ratio: 1.25),
(min-resolution: 120dpi) {
  .icheckbox_square-green,
  .iradio_square-green {
    background-image: url(green@2x.png);
    -webkit-background-size: 240px 24px;
    background-size: 240px 24px;
  }
}

